import { Component, NgModule } from '@angular/core';
import { IonicPage, NavController, NavParams, MenuController, IonicModule } from 'ionic-angular';
import { FncProvider } from '../../providers/fnc/fnc';
import { CnxProvider } from '../../providers/cnx/cnx';
import { TraPage } from '../tra/tra';

/**
 * Generated class for the TraColPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-tra-col',
  templateUrl: 'tra-col.html',
})
export class TraColPage {

  private _tra_col_ls = [];
  private _tracol_tt:string = null;
  private items: string[];

  constructor(
                public navCtrl: NavController,
                public navParams: NavParams,
                public menuCtrl: MenuController,
                public _fnc: FncProvider,
                public __Rq: CnxProvider,
              ) {
                this._Rst_Ls();
  }

  _Rst_Ls() {
    this.items = this._tra_col_ls;
  }

  //Verifica las columnas y las convierte en Array
  private _ChkTraCol(p=null){
    if(p.e == "ok"){
      for(var k in p.tra_col.ls) {
        this._tra_col_ls.push( p.tra_col.ls[k] );
      }
    }else{
      this._fnc.showAlert({ "dsc":p.w });
    }
  }

  //Filtrar datos por buscador
  private getItems(p: any) {
    this._Rst_Ls();

    const val = p.tt.target.value;
    if (val && val.trim() != '') {
      this.items = this.items.filter(item => item["tt"].toLowerCase().includes(val) || item["tt"].includes(val));
    }
  }

  private _Rq_Get_This(p=null){
    
    this._fnc.showLdr(); //Comienza el loader
    
    try{ //Peticion API/REST que trae las columnas
      this.__Rq._Rq_Get({ pml:"tra_col", get:"&cl_enc="+this._fnc.getSess("_cl").enc+"&tracol_tt="+this._tracol_tt }).subscribe(
        (data) => { this._ChkTraCol(data) },
        (error) => { console.log(error); }
      );
    }catch(err) {
      console.log(' Error al enviar post en tra-col.ts : '+err.message);
    }

    if( !this._fnc.isN(this._tracol_tt) ){
      this._fnc._ldr.dismiss(); //Finaliza el Loader
    }

  }
 
  private _Slc_Tra(p=null){
    this.navCtrl.push(TraPage, p);
  }

  ionViewDidLoad(){
    this._Rq_Get_This();
  }

}



