import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { FncProvider } from '../../providers/fnc/fnc'; //Clase de funciones
import { CnxProvider } from '../../providers/cnx/cnx'; //Clase de funciones

import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { ZBar } from '@ionic-native/zbar';


/**
 * Generated class for the DcRdrPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-dc-rdr',
  templateUrl: 'dc-rdr.html',
})
export class DcRdrPage {

  _data:any = { };

  constructor(
              private barcodeScanner: BarcodeScanner,
              private zbar: ZBar,
              public _fnc: FncProvider,
              public __Rq: CnxProvider,
            ) {
              this._data = {"cancelled":"-NA-", "format":"-NA-", "text":"-NA-" };
  }

  private _ScanBarCodeBtn(p=null){

    this.barcodeScanner.scan({

      preferFrontCamera : false, // iOS and Android
      showFlipCameraButton : true, // iOS and Android
      showTorchButton : true, // iOS and Android
      torchOn: false, // Android, launch with the torch switched on (if available)
      prompt : "Coloque el codigo de barras dentro del área.", // Android
      resultDisplayDuration: 500, // Android, display scanned text for X ms. 0 suppresses it entirely, default 1500
      formats : "PDF_417, QR_CODE", // default: all but PDF_417 and RSS_EXPANDED
      //orientation : "landscape", // Android only (portrait|landscape), default unset so it rotates with the device
      disableAnimations : false, // iOS
      disableSuccessBeep: false // iOS and Android

    }).then(barcodeData => {
        
      this._data = barcodeData;

      /*this.__Rq._Rq_Get({ pml:"barcode", get:"&code="+JSON.stringify(barcodeData) }).subscribe(
        (data) => {  },
        (error) => {  }
      );*/
        
    }).catch(err => {
        this._data = err;
    });

  }

  private _ScanZBarBtn(){

    this.zbar.scan({
      flash: 'off',
      drawSight: false
    }).then(result => {
        alert(2);
        console.log(result); // Scanned code
    })
    .catch(error => {
        alert(error);
        console.log(error); // Error message
    });

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad DcRdrPage');
  }

}



