import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { FncProvider } from '../../providers/fnc/fnc';

import { TraDtPage } from '../tra-dt/tra-dt';

/**
 * Generated class for the TraPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-tra',
  templateUrl: 'tra.html',
})
export class TraPage {

  private _tra_col_dt:any; //Detalle de la columna

  private _tra_ls = []; //Listado de tareas
  private items: string[];

  constructor(
                public navCtrl: NavController, 
                public navPrms: NavParams,
                public _fnc: FncProvider,
              ) {
    this._tra_col_dt = navPrms.data;
  }

  //Filtrar datos por buscador
  private getItems(p: any) {
    this._Rst_Ls();

    const val = p.tt.target.value;
    if (val && val.trim() != '') {
      this.items = this.items.filter(item => item["tt"].toLowerCase().includes(val) || item["tt"].includes(val));
    }
  }

  //Resetear datos
  private _Rst_Ls() {
    this.items = this._tra_ls;
  }
  
  //Verifica las columnas y las convierte en Array
  private _ChkTra(){
    this._fnc.showLdr(); //Comienza el loader

    if( !this._fnc.isN(this._tra_col_dt.tra.ls) ){
      for(var k in this._tra_col_dt.tra.ls) {
        this._tra_ls.push( this._tra_col_dt.tra.ls[k] );
      }
      this._Rst_Ls();
    }else{
      this._fnc.showAlert({ "dsc":"No existen tareas." });
    }

    this._fnc._ldr.dismiss(); //Finaliza el Loader
  }

  private _Slc_Tra(p=null){
    this.navCtrl.push(TraDtPage, p);
  }

  ionViewDidLoad() {
    this._ChkTra();
  }

}



